.title-wrapper {
  display: grid;
  width:100%;
  height:50vw;
  place-items: center;
  font-size: 10vmin;
  background-size: cover;
  /* background-attachment: fixed; */
  height: 40vh;
  color:var(--dark-blue);
  margin-bottom: 2vh;
  border-radius: 10px;
}

.img-text-wrapper {
  display: grid;
  place-items: center;
}

.img-text-wrapper > img {
  width:95%;
  /* border: 3px solid var(--dark-blue); */
  border-radius: 5px;
}

.content {
  width:100%;
}

h2 {
  margin-top:3ch;
  margin-bottom: 0vh;
}

p {
  padding:0vh 3vw;
  text-align: justify;
}

h2 + *, h3 + * {
  margin-top:0;
}

h3 {
  margin-bottom: 0.6rem;
  margin-top: 0.3rem;
  font-size: 1.1rem;
}

/* <-----------HOME-----------> */
.Quotes {
  display: grid;
  place-items: center;
}

.Quotes > .slider {
  height:100%;
  max-height: 200px;
  min-height: 100px;
}

.Quotes > .slider .animateOut, .Quotes > .slider .next {
  opacity: 0;
}
.Quotes > .slider .animateIn {
  opacity: 1;
}
.Quotes > .slideCounters {
  display: grid;
  grid-auto-flow: column;
  max-width: 300px;
  width:100%;
  place-content: space-between;
}
.Quotes > .slideCounters > div {
  width:1vmin;
  height:1vmin;
  background-color:var(--gray);
  border-radius: 100%;
  transition: background-color 0.3s ease-out;

}

.Quotes > .slideCounters > div.active {
  background-color: var(--dark-blue);
}


.track > .slide {
  margin:0 auto;
  width:100%;
  height:100%;
  display: grid;
  place-items: center;
}
.track > .slide > div {
  width: 80%;
  font-style: italic;
}

/* <-------ABOUT--------> */
#values-we-follow {
  display: grid;
  align-items: flex-start;
  justify-items:center;
  align-content: center;
  /* justify-content: center; */
  gap:2vh 2vw;
  padding-inline-start: 0;
  height:100%;
}

#values-we-follow li {
  list-style-type: none;
  display: grid;
  /* border: 3px solid var(--red); */
  /* border-radius: 5px; */
  max-width: 90vw;
  height: 100%;
  width:100%;
  /* overflow-y: auto; */
  padding:0 0.5rem;
  place-items: center;
}

