#board {
  display: grid;
  align-items: flex-start;
  justify-items:center;
  align-content: center;
  justify-content: center;
  gap:2vh 2vw;
  padding-inline-start: 0;
  width:100%;
}

#board li {
  list-style-type: none;
  display: block;
  /* border: 3px solid var(--dark-blue);
  border-radius: 5px; */
  /* border-bottom: 3px solid var(--dark-blue); */
  max-width: 90vw;
  height: 100%;
  width:100%;
  overflow-y: auto;
  padding-bottom: 2vh;
  padding-top: 1vh;
}
#board li:last-child {
  border: none;
}

#board img {
  width:100%;
  height:auto;
}

/* <-------ABOUT-------- /> */

.socials {
  text-decoration: none;
}

@media screen and (min-width: 768px) {
  .title-wrapper {
    height:70vh;
  }

  .img-text-wrapper {
    grid-auto-flow: column;
    grid-template-columns: 1fr 1fr;
  }

  #values-we-follow {
    grid-auto-flow: column;
  }
  #board li {
    /* grid-auto-flow: column; */
    place-items: center;
    display:grid;
    place-items: center;
  }

  #board img {
    width:75%;
  }
  /* #board li {
    border:unset;
    border-right: 3px solid var(--dark-blue);
  } */
  p {
    /* width:80%; */
    padding:0;
  }
}

.content > .socials {
  display: grid;
  grid-auto-flow: row;
  gap:4vh;
}

.content > .socials > a {
  display: grid;
  grid-auto-flow: column;
  gap:2vw;
  justify-content: start;
}

hr {
  /* margin=bott:0; */
  width:15rem;
  border-color: var(--pastel-blue);
  border-style: solid;
  background: var(--pastel-blue);
}